.App {
  text-align: left;
}

.cmdBlock {
  background: #202225;
  border-color: #000;
  color: #72767d;
  border-radius: 0px;
  margin-bottom: 10px;
  max-width: 800px;
}

.blockMain {
  background: #5A5A5A;
  padding: 10px;
}

.tagSelector {
  outline: none;
  text-transform: capitalize;
}

.tagSelector {
  cursor: pointer;
  padding: 15px;
  margin: 10px;
  user-select: none;
  color: #fff !important;
}

.tagSelector-active {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  font-weight: 500;
  list-style: none;
  box-sizing: border-box;
  vertical-align: baseline;
  cursor: pointer;
  padding: 10px 20px;
  margin: 0;
  border-radius: 0;
  text-transform: capitalize;
  border: 1px solid rgba(0,0,0,.2);
  background: #15b8e8;
}

.tagSelector-inactive {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  font-style: normal;
  font-weight: 500;
  list-style: none;
  box-sizing: border-box;
  vertical-align: baseline;
  text-decoration: none;
  cursor: pointer;
  background: #f7f7f9;
  padding: 10px 20px;
  margin: 0;
  border-radius: 0;
  text-transform: capitalize;
  background-color: #313339;
  border: 1px solid rgba(0,0,0,.2);
}

.cmd-heading {
  cursor: pointer;
  user-select: auto;
}

.searchInput {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  font: inherit;
  font-size: 16px!important;
  background: #eee;
  font-family: inherit;
  margin: 0;
  overflow: visible;
  display: block;
  width: 100%;
  padding: 6px 12px;
  line-height: 1.42857143;
  background-image: none;
  transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
  background-color: #2b2f33!important;
  border-radius: 3px;
  box-shadow: none;
  color: #fff;
  height: 42px;
  box-sizing: border-box;
  border: 1px solid #000!important;
}

h5, h6  {
  display: inline;
}

h6 {
  color: #bfbfbf;
}

.card-title {
  color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
